import request from '@/common/utils/request';
import { buildUrlWithObj } from '@/common/utils/url-utils';
import FileUtil from '@/common/utils/file-utils';

const API_PREFIX = '/fintech/v1/bulk';
const TASK_TEMPLATE_PREFIX = API_PREFIX + '/task/template';
const BULK_TASK_PLAN_PREFIX = API_PREFIX + '/task/plan';

// 
export function queryBulkDataDicts({ dictCodes }) {
  return request({
    url: API_PREFIX + '/data-dict/list',
    method: 'post',
    data: { dictCodes },
  });
}

// bulk API
export function queryBulkTypes(data) {
  return request({
    url: buildUrlWithObj(API_PREFIX + '/type/list', data),
    method: 'get',
  });
}

// bulk
export function queryBulkTaskTemplates(data) {
  return request({
    url: buildUrlWithObj(TASK_TEMPLATE_PREFIX + '/list', data),
    method: 'get',
  });
}

// Bulk
export function queryTaskPlanList(data) {
  return request({
    url: buildUrlWithObj(BULK_TASK_PLAN_PREFIX + '/list', data),
    method: 'get',
  });
}

export function queryTimeSlot(params) {
  return request({
    url: buildUrlWithObj('/fintech/v1/bulk/tps/timeslot', params),
    method: 'get',
  });
}

export function addTaskPlan(data) {
  return request({
    url: '/fintech/v1/bulk/task/plan/add',
    method: 'POST',
    data,
  });
}

export function cancelTaskPlan(data) {
  return request({
    url: '/fintech/v1/bulk/task/plan/cancel',
    method: 'POST',
    data,
  });
}

export function suspendTaskPlan(data) {
  return request({
    url: '/fintech/v1/bulk/task/plan/suspend',
    method: 'POST',
    data,
  });
}
export function resumeTaskPlan(data) {
  return request({
    url: '/fintech/v1/bulk/task/plan/resume',
    method: 'POST',
    data,
  });
}

export function getFailedRecords(params) {
  return request({
    url: '/fintech/v1/bulk/task/plan/failedRecords',
    params,
  });
}

export function getSuccessRecords(params) {
  return request({
    url: '/fintech/v1/bulk/task/plan/successRecords',
    params,
  });
}

export function queryValidRecords(params) {
  return request({
    url: '/fintech/v1/bulk/task/plan/validRecords',
    params,
  });
}

export function getTaskPlanDetail(data) {
  return request({
    url: buildUrlWithObj('/fintech/v1/bulk/task/plan/detail', data),
    method: 'get',
  });
}

export function queryBulkHistoryDetail(data) {
  return request({
    url: buildUrlWithObj('/fintech/v1/bulk/task/plan/hisDetail', data),
    method: 'get',
  });
}

export function preValidation(data) {
  return request({
    url: BULK_TASK_PLAN_PREFIX + '/prevalidation',
    method: 'post',
    contentType: 'application/x-www-form-urlencoded',
    data,
  });
}

export function queryPreValidationResult(fileID) {
  return request({
    url: BULK_TASK_PLAN_PREFIX + `/prevalidation/${fileID}`,
    method: 'get',
  });
}

export function downloadBulkFile(fileID, type) {
  FileUtil.downLoadFile(
    'orgportal' + BULK_TASK_PLAN_PREFIX + `/download/bulkFile?fileID=${fileID}&type=${type}`,
    {},
    'get'
  );
}
// customer
export function getCustomerList(data) {
  return request({
    url: '/fintech/v1/queryCustomerList',
    method: 'post',
    data,
    ignoreError: true,
  });
}
